:root {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: white;
  font: 100%/1.5 sans-serif;
}

.app {
  display: grid;
  grid-column-gap: 2rem;
  grid-template-columns: 18.75rem auto;
  margin: 0 auto;
  max-width: 56.25rem;
  width: 90%;
}

.app > *:first-child,
.app > *:last-child {
  border: 5px solid black;
}

.foods {
  height: 90vh;
  line-height: 1.2;
  list-style: none;
  margin: 0;
  overflow-y: scroll;
  padding: 1rem;
}

.foods li {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 0 2rem;
  padding: 0;
}

.foods li.inactive {
  opacity: 0.3;
}

.foods img {
  margin-bottom: 1rem;
  max-height: 6.5rem;
  max-width: 6.5rem;
}

.foods h2 {
  margin: 0 0 1rem;
  text-transform: uppercase;
}

.foods p {
  margin: 0;
  text-align: center;
}

.admin {
  height: 90vh;
  overflow-y: scroll;
  padding: 1rem;
}

.sign-edit {
  border: 1px solid #111;
  margin-bottom: 1rem;
}

.controlgroup {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

label {
  color: darkgray;
  font-size: 80%;
}

input[type="text"],
select,
textarea {
  padding: 0.5rem;
}
